import React from "react";
import "./Souls.css";
function Souls() {
  const handleClick = () => {
    window.open("tel:000");
  };
  return (
    <div className="btn-container" onClick={handleClick}>
      <button type="submit">S.O.S</button>
      <h1>WHAT IS THE SOS BUTTON AND HOW DO I USE THIS?</h1>
      <p>
        <strong>
          The SOS key will allow you to send a predefined custom SMS message,
          including your current location, to up to three predefined contacts
          through this web app. The location shared by the app will be
          established by your mobile device, and only sent to the message
          recipients you specify.
        </strong>
      </p>
    </div>
  );
}
export default Souls;
